<template xmlns:v-slot = "http://www.w3.org/1999/XSL/Transform">
  <v-app>
    <v-main class="px-3 py-3">
      <v-snackbar v-model="installDialog" vertical color="white" :timeout="installDialogTimeout">
        <p class="text--black">Would you want to install the app?</p>
        <br/>
        <v-btn color = "green" @click = "installApp()" block dark rounded >Yes, Install It!</v-btn>
        <v-btn color = "red" @click = "disMissInastall()" block text>No</v-btn>
      </v-snackbar>  
      <v-row justify = "center" content = "center">
        <v-col cols = "12" md = "4">
          <v-card-title>ToDo You're Works</v-card-title>
          <v-card class="px-3 py-3">
            <v-row justify = "center" content = "center">
              <v-col cols = "12" sm = "6" md = "12">
                <v-text-field label="What is your work title?" placeholder="write you're title here..." outlined v-model = "todo.title"></v-text-field>
              </v-col>
              <v-col cols = "12" sm = "6" md = "12">
                <v-textarea label="What To Do?" placeholder="write you're work here..." outlined v-model = "todo.content"></v-textarea>
              </v-col>

              <v-col cols = "12" sm = "6" md = "12">
                <v-btn color = "green" @click = "saveToDo()" block dark>Save To Do</v-btn>
              </v-col>
            </v-row>
            <br>
            <div v-if = "todos.length">
              <hr>

              <v-list flat subheader three-line >
                <v-subheader>To DO List:</v-subheader>

                <v-list-item-group multiple active-class="">
                  
                  <div v-for="(todo, index) in todos" :key="index">
                    <v-list-item>
                      <template v-slot:default = "{ active }">
                        <v-list-item-action>
                          <v-checkbox v-model = "todo.active"></v-checkbox>
                        </v-list-item-action>

                        <v-list-item-content>
                          <v-list-item-title>{{ todo.title }}</v-list-item-title>
                          <v-list-item-subtitle>{{ todo.content }}</v-list-item-subtitle>
                        </v-list-item-content>
                      </template>
                    </v-list-item>
                  </div>

                </v-list-item-group>
              </v-list>

              <v-col cols = "12" sm = "6" md = "12">
                <v-btn color = "red" @click = "removeToDo()" block dark>Remove To Do</v-btn>
              </v-col>
              
            </div>
          </v-card>
        </v-col>
      </v-row>
    </v-main>
  </v-app>
</template>
<script>

export default {
  name: 'App',

  components:{},

  data: () => ({
    todo:{
      title: '',
      content: '',
      active: false,
    },
    todos: [],

    installDialog: false,
    installDialogTimeout: 10000,
    deferredPrompt: null,
  }),

  watch:{
    todos:{
      handler(){
        localStorage.setItem('todos', JSON.stringify(this.todos))
      }
    }
  },

  methods:{
    saveToDo() {
      let emptyToDo = {
        title: '',
        content: '',
        active: false
      };
      if (this.todo.title.length > 0){
        this.todos.push(this.todo);
      }
      this.todo = emptyToDo;
    },
    removeToDo() {
      this.todos.forEach((todo, index) => {
        if (todo.active === true) {
          this.todos.splice(index);
        }
      });
    },
    installApp(){
      this.installDialog = false;
      this.deferredPrompt.prompt();
      this.deferredPrompt.userChoice.then((choiceResult) => {
        if(choiceResult.outcome === 'accepted'){
          console.log('user accept the A2HS prompt');
          //this.installDialog = false;
        }else{
          console.log('user dismissed the A2HS prompt');
          //this.installDialog = false;
        }
        //
        this.deferredPrompt = null;
      });
    },
    disMissInstall(){
      this.installDialog = false;
    },
  },

  mounted() {
    window.addEventListener('beforinstallprompt', (e) => {
      e.preventDefault();
      
      this.deferredPrompt = e;
      if(this.ismobileCheck){
        this.installDialog = true;
      }
      
    });
    
    if(localStorage.getItem('todos')){
      this.todos = JSON.parse(localStorage.getItem('todos'))
    }
  }
};
</script>
